* {
	padding: 0;
	margin: 0;
}
body {
	margin: 0 auto 0 auto;
	padding: 0;
	max-width: 1200px;
	font-family: "Avenir", "Avenir Next", Helvetica Neue, Arial;
	font-size: 0.95em;
}
a,
a:visited {
	text-decoration: none;
	color: #7533f4;
}
a:hover {
	text-decoration: underline;
	color: #f4b014;
}
h1,
h2,
h3,
h4,
h5 {
	color: #492a7c;
	background-color: inherit;
	font-weight: normal;
	padding: 0 0 5px 0;
	margin: 15px 0 0 0;
	border: none;
	clear: right;
}
h1 {
	font-size: 24pt;
	margin: 5px 0 10px 0;
	line-height: 28px;
}
h2 {
	font-size: 14pt;
	margin: 30px 0 15px 0;
	letter-spacing: 0.01em;
	border-bottom: 1px solid #ccc;
	line-height: 20px;
}
h3 {
	font-size: 13pt;
}
h4 {
	font-size: 12pt;
}
h5 {
	font-size: 11pt;
}
p {
	margin: 0 0 10px 0;
}
.content {
	margin: 0;
	padding: 15px 20px;
	background-color: #ffffff;
}
.title,
.title h1,
.title a {
	color: #492a7c;
	font-size: 24pt;
	margin-bottom: 20px;
	margin-top: 5px;
}
.footer {
	border-top: 1px solid #ccc;
	margin-top: 30px;
	padding-top: 4px;
	text-align: right;
	font-size: 12px;
}
.footer a {
	color: #21346b;
}
.footer a:hover {
  color: #ce3333;
}

.line {
	fill: none;
	stroke: steelblue;
	stroke-width: 2px;
}

.axisSteelBlue text {
	fill: steelblue;
}

.axisRed text {
	fill: red;
}

/* Added for only DD3 Testing */
svg#dd3bar-svg {
  display: block;
  width: 400%;
  height: 100%;
}

div#dd3-bar {
  width: 1000px;
  height: 800px;
  margin: auto;
  background-color: #2F4A6D;
  overflow-x: scroll;  /* for scrolling */
}

.bar {
  fill: #80cbc4;
}

line {
  stroke: gray;
}

line#limit {
  stroke: #FED966;
  stroke-width: 3;
  stroke-dasharray: 3 6;
}

.grid path {
  stroke-width: 0;
}

.grid .tick line {
  stroke: #9FAAAE;
  stroke-opacity: 0.3;
}

text.divergence {
  font-size: 14px;
  fill: #FED966;
}

/* The following is optional */
text.value {
  font-size: 14px;
}

text.title {
  font-size: 22px;
  font-weight: 600;
}

text.label {
  font-size: 14px;
  font-weight: 600;
}

text.source {
  font-size: 10px;
	color: #ce3333;
}
